$white:#FFFFFF;
$dark-red:#650300;
$red:#AA0601;
$y-white:#FBF2F2;
$black:#000000;
$gray:#00000029;
$elephant:#DEE2E6;
$dark-gray:#707070;

*{
    font-family: 'Noto Serif TC', serif;
}
h1,h2,h3,p,li{
    line-height: 1.5em;
}
a,a:hover{
    display: block;
    color: $white;
    text-decoration:none;
}
.wrap{
    margin: 0 auto;
    max-width: 1920px;
}
.container-txt{
    margin: 0 auto;
    max-width: 67.5%;
}
.page-title{
    border-left: 8px solid $dark-red;
    padding-left: 16px;
    font-size: 48px;
    box-sizing: border-box;
    margin-bottom: 32px;
}
img{
    max-width: 100%;
    vertical-align: bottom;
}
button{
    border: none;
    font-size: 20px;
}
.layer-darkRed{
    background-color: $red;
    color: $white;
}
.flex-spb{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.flex-cen{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.flex-str{
    display: flex;
    flex-wrap: wrap;
    justify-content: start;
}
.flex-col{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: start;
}
.switcbox{
    margin-bottom: 80px;
    li{
        font-size: 18px;
        line-height: 1.5em;
        padding: 12px 16px;
        border: 1px solid #000;
    }
    li:hover{
        background-color: $black;
        color: $white;
    }
    li:first-child,
    li:last-child{
        font-size: 19px;
        padding: 11px 12px;
    }
}
@media screen and (max-width: 768px) {
    .container-txt {
        margin: 0 auto;
        max-width: 696px;
    }
    .db-ipad{
        display: block;
      }
  }
@media screen and (max-width: 375px) {
    .container-txt{
        margin: 0 auto;
        max-width: 95%;
    }
    .page-title{
        font-size: 24px;
    }
  }
  
