@import "reset";
@import "header";
@import "footer";

li,p,h1,h2,span{
    line-height: 1.5em;
}
.wrap-nav{
    background-color: $dark-red;
    color: $white;
    margin-bottom: 96px;
    li{
    font-size: 20px;
    padding: 13px 0;  
    }
    li+li{
       margin-left: 48px; 
    }
}
h1{
    border-left: 8px solid $dark-red;
    padding-left: 16px;
    font-size: 48px;
    box-sizing: border-box;
    margin-bottom: 32px;
}
.card-list{
    margin-bottom: 124px;
}
.card{
    position: relative;
    margin-bottom: 60px;
    .img{
        width: 49.1%;
        margin-right: 1.8%;
    }
    .content{
        width: 49.1%;
    }
}
.card>.content{
    h2{
        font-size: 32px;
        color: $dark-red;
    }
    .time{
        align-items: center;
        margin-bottom: 16px;
        p:first-child{
            font-size: 20px;
            font-weight: 600;
        }
        p:last-child{
            font-size: 16px;
        }
    }
    p{
        font-size: 20px;
    }
    a{
        position: absolute;
        bottom: 0;
        right: 0;
       font-size: 36px;
       color: $dark-red;
    }
}
@media screen and (max-width: 768px) {
    .card{
        overflow: hidden;
        //height: 208px;
    }
    a{.mobile-block{
        display: block; 
        .img{
          width: 100%;
        }
        .content{
          width: 100%;
        }
      }
        background-color: #fff;
    }
}
@media screen and (max-width: 375px) {
    nav{
        display: none;
    }
    h1{
        margin-top: 40px;
    }
    .mobile-block{
        display: block;
    }
    .card{
        overflow: auto;
    }
    .card>.img{
        width: 100%;
    }
    .card>.content{
        width: 100%;
    }
    .time>p{
        font-size: 16px;
    }
    .content>p,
    a{
        display: none;
    }
    .switcbox>li{
        font-size: 16px;
    }
}