header{
    position: relative;
}
.header {
    justify-content: space-between;
    padding: 25px 0;
}
.logo img {
    width: 80px;
}

.main-menu {
    li>a {
        font-size: 20px;
        line-height: 30px;
        margin-right: 64px;
    }

    li:last-child>a {
        margin-right: 0px;
    }
}
.sub-menu-wrap{
    display: none;
    background-color: #fff;
    position: absolute;
    width: 100%;
    top: 100%;
    left: 0;
}
.sub-menu { 
    background-color: #fff;
    li {   
        width: 33.3333%;
        box-sizing: border-box;
    }
    a+a{
        border-left: 1px solid #000;
    }
    a{
        width: 100%;
        padding: 24px 0;
        font-size: 32px;
        color: $black;
        text-align: center;
    }
    a.actived,
    a:hover{
        border-bottom: 8px solid $red;
    }
}
.switch-header:hover .sub-menu-wrap{ 
    display: block;
}
@media screen and (max-width: 375px) {
    .main-menu {
        display: none;
    }
}